s.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Montserrat:wght@400;500&display=swap');\r\n\r\n/*===== VARIABLES CSS =====*/\r\n\r\n\r\n$header-height: 100px;\r\n\r\n$base-font-size: 40px;\r\n/*========== Colors ==========*/\r\n$first-color: #4FB2E5;\r\n$second-color: rgba(79, 178, 229, 0.2);\r\n$title-color: #2F6E89;\r\n$text-color: #4B4B4B;\r\n$text-color-light: #FFFFFF;\r\n\r\n/*========== Font and typography ==========*/\r\n$body-font: 'Lato', sans-serif;\r\n$biggest-font-size: 56px;\r\n$h2-font-size: 32px;\r\n$h3-font-size: 17px;\r\n$normal-font-size: 16px;\r\n$font-semi-bold: 600;\r\n$font-bold: 700;\r\n$font-black: 900;\r\n$font-regular: 400;\r\n$z-fixed: 100;\r\n/*========== BASE ==========*/\r\n*, ::before, ::after {\r\n  box-sizing: border-box;\r\n  margin: 0;\r\n  padding: 0;\r\n}\r\n\r\nhtml {\r\n  scroll-behavior: smooth;\r\n}\r\n\r\n\r\nbody {\r\n  margin: 0;\r\n  font-family: $body-font;\r\n  font-size: $normal-font-size;\r\n  color: $text-color;\r\n}\r\n\r\nul {\r\n  padding: 0;\r\n  list-style: none;\r\n}\r\n\r\na {\r\n  text-decoration: none;\r\n  color: inherit;\r\n}\r\n\r\nimg {\r\n  max-width: 100%;\r\n  height: auto;\r\n}\r\n\r\nh1 {\r\n  font-weight: $font-black;\r\n  font-size: $biggest-font-size;\r\n  color: $title-color;\r\n  line-height: 73px;\r\n}\r\n\r\nh2 {\r\n  font-weight: $font-bold;\r\n  font-size: $h2-font-size;\r\n  color: $title-color;\r\n  text-align: center;\r\n}\r\n\r\nh3 {\r\n  font-weight: $font-regular;\r\n  font-size: $h3-font-size;\r\n  color: $title-color;\r\n  line-height: 20px;\r\n  letter-spacing: 1px;\r\n}\r\n\r\np {\r\n  font-weight: 300;\r\n  font-size: $normal-font-size;\r\n  line-height: 28px;\r\n  color: $text-color;\r\n}\r\n\r\nh1, h2, h3, ul, p {\r\n  margin: 0;\r\n}\r\n\r\n//###################################### HEADER ################################\r\n.main-header {\r\n  position: fixed;\r\n  z-index: 5;\r\n  width: 100%;\r\n  padding: 30px 0;\r\n  transition: 1s;\r\n}\r\n\r\n.sticky {\r\n  background: #fff;\r\n  padding: 10px 0;\r\n  box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.1);\r\n\r\n}\r\n\r\n//###################################### NAV ####################################\r\n\r\nnav.main-nav {\r\n  max-width: 90%;\r\n  margin: auto;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n\r\n  .nav__menu-list {\r\n    display: inline-flex;\r\n    align-items: center;\r\n\r\n    li.nav__item.reservation a {\r\n      color: white;\r\n      line-height: 17px;\r\n      text-transform: uppercase;\r\n    }\r\n\r\n    .nav__item {\r\n\r\n\r\n      margin-left: 25px;\r\n\r\n      & a {\r\n        transition: all .5s ease;\r\n        text-transform: uppercase;\r\n        color: $text-color;\r\n        font-size: calc(13px + .2vw);\r\n        font-weight: 500;\r\n\r\n      }\r\n\r\n      & a:hover {\r\n        color: $first-color;\r\n        font-weight: bolder;\r\n      }\r\n\r\n      &.reservation {\r\n\r\n        display: block;\r\n        letter-spacing: 0.5px;\r\n        border-radius: 5px;\r\n        padding: 10px 9px;\r\n        border: 1px solid white;\r\n        margin-left: calc(20px + 8vw);\r\n        background-color: $first-color;\r\n      }\r\n\r\n    }\r\n\r\n  }\r\n}\r\n\r\nion-icon {\r\n  color: $text-color;\r\n  font-size: 30px;\r\n  cursor: pointer;\r\n  --ionicon-stroke-width: 60px;\r\n  display: none;\r\n}\r\n\r\n.menu-close {\r\n  position: absolute;\r\n  top: 20px;\r\n  right: 30px;\r\n}\r\n\r\n\r\n//######################################## MAIN #############################################\r\n//###### ############################REUSABLE #########################################\r\n.section__description {\r\n  font-weight: normal;\r\n  font-size: calc(10px + .5vw);\r\n  line-height: 1.5;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.06vw;\r\n  color: $title-color;\r\n\r\n\r\n}\r\n\r\n//###### ############################ #########################################\r\nmain {\r\n  min-height: 2000px;\r\n}\r\n\r\n.container {\r\n  width: 90%;\r\n  margin: 0 auto;\r\n}\r\n\r\n.section {\r\n\r\n}\r\n\r\n.section-two-column {\r\n  padding-top: 180px;\r\n  background-color: $second-color;\r\n  padding-left: 3rem;\r\n  padding-right: 1rem;\r\n\r\n  display: flex;\r\n  justify-content: space-between;\r\n\r\n  .section-two-column-text {\r\n    max-width: 33vw;\r\n    padding-top: 8%;\r\n\r\n    .section-two-column-title {\r\n      max-width: 80%;\r\n      margin-top: 1.7vw;\r\n      font-style: normal;\r\n      font-weight: 900;\r\n      font-size: 3.8vw;\r\n      line-height: 5vw;\r\n    }\r\n\r\n    .section-two-column-paragraph {\r\n      font-style: normal;\r\n      font-weight: 300;\r\n      line-height: 1.92vw;\r\n      color: $text-color;\r\n      padding: 3vw 0;\r\n\r\n\r\n    }\r\n  }\r\n\r\n  .section-two-column-img {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    width: 60%;\r\n  }\r\n}\r\n\r\n//####################### services__section ##################\r\n.services__section {\r\n  padding: 10vw 3rem 1rem;\r\n\r\n  .section__description {\r\n    text-align: center;\r\n\r\n  }\r\n\r\n  .section-bold-text {\r\n    margin-top: .5vw;\r\n    margin-bottom: 3vw;\r\n  }\r\n\r\n  .tiles-wrapper {\r\n\r\n    display: grid;\r\n    grid-template-columns: repeat(3, 1fr);\r\n    max-width: 100%;\r\n    gap: 1rem;\r\n\r\n    .tile {\r\n      border: 2px solid #4FB2E5;\r\n      box-shadow: 10px 10px 0px #4FB2E5;\r\n      border-radius: 24px;\r\n      text-align: center;\r\n      color: #4B4B4B;\r\n\r\n      &:nth-last-child(1) {\r\n        margin: 0 auto;\r\n        width: 100%;\r\n        margin-left: 50%;\r\n        transform: translateX(55%);\r\n      }\r\n\r\n      .tile-icon {\r\n        margin: 5% auto;\r\n        background: #4FB2E5;\r\n        border-radius: 50%;\r\n        width: 100px;\r\n        padding: 20px;\r\n\r\n        img {\r\n          width: 50px;\r\n        }\r\n      }\r\n\r\n      .tile-title {\r\n        font-style: normal;\r\n        font-weight: bold;\r\n        font-size: 2vw;\r\n        line-height: 2vw;\r\n      }\r\n\r\n      .tile-description {\r\n        padding: 5%;\r\n      }\r\n\r\n    }\r\n  }\r\n}\r\n\r\n.about-us__section {\r\n  column-gap: 125px;\r\n  justify-content: center;\r\n  background-color: #fff;\r\n  padding-left: 2rem;\r\n  padding-right: 2rem;\r\n  padding-top: 100px;\r\n\r\n  .section-two-column-text {\r\n    .about-us__heading {\r\n      text-align: left;\r\n      margin: 4% 0;\r\n    }\r\n\r\n    .about-us__paragraph {\r\n      padding: 0;\r\n    }\r\n  }\r\n\r\n\r\n  .section-two-column-text {\r\n    .features-list {\r\n      text-align: center;\r\n      li.feature {\r\n        text-align: left;\r\n        display: flex;\r\n        align-items: center;\r\n        margin-top: 15px;\r\n        font-weight: bold;\r\n        font-size: calc(18px + .1vw);\r\n        color: $title-color;\r\n\r\n\r\n      }\r\n\r\n      .li-sign {\r\n        width: 18px;\r\n        margin-right: 5px;\r\n      }\r\n\r\n    }\r\n  }\r\n}\r\n.personnel__section{\r\n  margin-top: 8%;\r\n  .person-wrapper{\r\n    margin: 50px auto;\r\n   width: 60%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    .person{\r\n\r\n      .person-name{\r\n        font-size: 1.8vw;\r\n        color: $text-color;\r\n      }\r\n      .person-position{\r\n        margin-top: 10px;\r\n        text-align: center;;\r\n      }\r\n    }\r\n  }\r\n}\r\n.phone-button {\r\n\r\n  border-radius: 10px;\r\n  border: 2px solid $title-color;\r\n  margin-top: 5%;\r\n\r\n  .phone-wrapper{display:flex ;\r\n    text-align: center;\r\n    justify-content: center;\r\n    padding: 10px 30px;\r\n\r\n  }\r\n}\r\nion-icon.phone{\r\n  display: block;\r\n  font-size: 20px;\r\n}\r\n@media screen and (max-width: 1024px) {\r\n  nav.main-nav {\r\n    max-width: 98%;\r\n\r\n    .nav__menu-list {\r\n      .nav__item {\r\n        margin-left: 10px;\r\n      }\r\n\r\n      .nav__item.reservation {\r\n        margin-left: 20px;\r\n      }\r\n    }\r\n  }\r\n\r\n  .services__section {\r\n    .tiles-wrapper {\r\n      grid-template-columns: repeat( auto-fit, minmax(250px, 1fr));\r\n\r\n      .tile {\r\n        &:nth-last-child(1) {\r\n          margin-left: 0;\r\n          transform: translateX(0);\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .about-us__section {\r\n    column-gap: 8%;\r\n    .section-two-column-text{\r\n      .features-list li.feature{\r\n        font-size: 18px;\r\n      }\r\n    }\r\n\r\n  }\r\n\r\n}\r\n\r\n@media screen and (max-width: 768px) {\r\n\r\n\r\n  .main-header {\r\n    padding: 10px 0;\r\n  }\r\n  ion-icon {\r\n    display: block;\r\n\r\n  }\r\n  .hide {\r\n    display: none;\r\n  }\r\n\r\n  nav.main-nav {\r\n    max-width: 90%;\r\n    margin: 0 8px;\r\n\r\n    .logo {\r\n      max-width: 85%;\r\n    }\r\n\r\n    .nav__menu-list {\r\n      position: fixed;\r\n      top: 0;\r\n      left: -100%;\r\n      height: 100vh;\r\n      width: 100%;\r\n      max-width: 400px;\r\n      background-color: $first-color;\r\n      padding: 60px 0;\r\n      text-align: center;\r\n      display: block;\r\n      transition: all .5s ease;\r\n\r\n      .nav__item {\r\n        margin-top: 45px;\r\n        font-size: 23px;\r\n      }\r\n\r\n      .reservation {\r\n        margin: 30px 25px;\r\n      }\r\n    }\r\n\r\n    .nav__menu-show {\r\n      left: 0;\r\n    }\r\n  }\r\n  .section__description {\r\n    font-size: 18px;\r\n\r\n  }\r\n  .section-two-column {\r\n    padding-left: 1rem;\r\n    padding-right: 1rem;\r\n    padding-top: 100px;\r\n    flex-direction: column-reverse;\r\n    justify-content: center;\r\n    align-items: center;\r\n\r\n    .section-two-column-text {\r\n      max-width: 100%;\r\n      padding-top: 4%;\r\n      text-align: center;\r\n\r\n      .section-two-column-title {\r\n        max-width: 100%;\r\n        margin: 25px 0;\r\n        font-size: 27px;\r\n        line-height: 1.2;\r\n      }\r\n\r\n      .section-two-column-paragraph {\r\n        font-size: 18px;\r\n        line-height: 1.3;\r\n        margin: 40px 0;\r\n      }\r\n    }\r\n\r\n    .section-two-column-img {\r\n      width: 100%;\r\n    }\r\n  }\r\n  .services__section {\r\n    .tiles-wrapper {\r\n      padding: 0 1rem;\r\n      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));\r\n\r\n      .tile .tile-title {\r\n        font-size: 20px;\r\n        margin: 20px 0;\r\n      }\r\n\r\n    }\r\n  }\r\n  .about-us__section {\r\n    padding-top: 30px;\r\n    flex-direction: column;\r\n\r\n    .section-two-column-text {\r\n      .about-us__heading {\r\n        text-align: center;\r\n      }\r\n      .features-list{\r\n        width: fit-content;\r\n        padding-left: 20px;\r\n        margin: 40px 0;\r\n        li.feature {\r\n          font-size: 18px;\r\n          display: block;\r\n          text-align: left;\r\n        }\r\n      }\r\n       \r\n    }\r\n  }\r\n}\r\n\r\n@media screen and (max-width: 500px) {\r\n  .section {\r\n    padding-left: 1rem;\r\n    padding-right: 1rem;\r\n  }\r\n  .section-bold-text {\r\n    font-size: 22px;\r\n  }\r\n  .services__section {\r\n    padding: 20px 10px 10px;\r\n\r\n    .tiles-wrapper {\r\n      padding: 0 15px 15px;\r\n\r\n      .tile .tile-title {\r\n        font-size: 18px;\r\n      }\r\n    }\r\n  }\r\n  .section__description {\r\n    font-size: $normal-font-size;\r\n\r\n  }\r\n\r\n  .section-two-column .section-two-column-text .section-two-column-title {\r\n    font-size: 22px;\r\n  }\r\n  .section-two-column .section-two-column-text .section-two-column-text {\r\n    font-size: $normal-font-size;\r\n    .section-two-column-paragraph{\r\n      font-size: $normal-font-size;\r\n    }\r\n  }\r\n  .services__section .tiles-wrapper .tile .tile-description {\r\n    padding: 0;\r\n  }\r\n  .about-us__section .section-two-column-text .features-list li.feature{\r\n    text-align: center;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
